/* src/layouts/MainLayout.css */

.layout-container {
  display: flex;
  height: 100vh;
}

.main-content {
  flex: 1;
  padding: 1rem;
  transition: margin-left 0.3s ease;
  overflow-y: auto;
  margin-left: 240px; /* default sidebar width */
}

.main-content.collapsed {
  margin-left: 80px; /* collapsed sidebar width */
}
